body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


.app {
  text-align: center;
  padding: 20px;
}

.navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 20px;
}

.navbar-brand {
  display: flex;
  align-items: center;
}

.brand-icon {
  font-size: 32px;
  margin-right: 8px;
}

.brand-name {
  font-size: 24px;
}

.get-users-btn {
  font-size: 16px;
  padding: 8px 16px;
  background-color: #36d7b7;
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  transition: background-color 0.3s;
}

.get-users-btn:hover {
  background-color: #2fb89f;
}

.get-users-btn:disabled {
  opacity: 0.5;
  cursor: not-allowed;
}

.loading-icon {
  -webkit-animation: spin 1s linear infinite;
          animation: spin 1s linear infinite;
}

@-webkit-keyframes spin {
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes spin {
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.user-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
  grid-gap: 20px;
  gap: 20px;
}

.user-card {
  background-color: #f5f5f5;
  padding: 20px;
  border-radius: 4px;
}

.avatar {
  width: 100%;
  max-width: 200px;
  height: auto;
  margin-bottom: 10px;
}

.user-name {
  font-size: 20px;
  margin-bottom: 8px;
}

.user-email {
  font-size: 14px;
  color: #777;
}


